.container {
  width: 80%;
  margin: 0 auto;
}

.none {
  display: none;
}

header {
  .logo {
    font-size: 24px;
    font-weight: bold;

    &.margarine-regular {
      font-family: "Margarine", sans-serif;
      font-weight: 400;
      font-style: normal;
      padding-right: 30px;
    }
  }

  .logo-img {
    width: 70px;
    height: 70px;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
}



  .input-area {
    padding: 30px 0;
    text-align: center;

    .container {
      width: 220px;

      h2 {
        font-weight: 500;
        margin-bottom: 40px;
      }

      input {
        color: black;
        display: block;
        width: 100%;
        height: 50px;
        margin-bottom: 20px;
        padding: 0 10px;
        border: 1px solid var(--green1-color);
        border-radius: 10px;
        background-color: var(--green1-color);
      }

      .wrapper {
        width: 100%;
        display: flex;
        justify-content: space-between;
        align-items: center;
      }

      button {
        display: block;
        margin: 0 auto;
        width: 220px;
        height: 33px;
        background-color: #578057;
        color: var(--white-color);
        border: none;
        border-radius: 30px;
        cursor: pointer;

        &.disable {
          cursor: not-allowed;
          background-color: gray;
        }
      }

      .verify-link-sent {
        h2 {
          margin-bottom: 10px;
          font-size: 14px;
        }

        p {
          font-size: 12px;
          margin-bottom: 10px;
        }
      }
    }
  }

footer {
  background-color: var(--white-color);
  color: var(--footer-font-color);
  text-align: center;
  padding: 20px 0;
}