.loader-box {
  position: absolute;
  top: 25%;
  left: 50%;
  transform: translate(-25%, -50%);
}
.loader {
  border: 4px solid rgba(255, 255, 255, .6);
  border-left-color: transparent;
  border-radius: 50%;
}

.loader {
  border: 4px solid rgba(255, 255, 255, .6);
  border-left-color: transparent;
  width: 36px;
  height: 36px;
}

.loader {
  border: 4px solid rgba(255, 255, 255, .6);
  border-left-color: transparent;
  width: 36px;
  height: 36px;
  animation: spin89345 1s linear infinite;
}

@keyframes spin89345 {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}